import React, { useState, useRef, useEffect, useCallback, useImperativeHandle, forwardRef } from 'react';
import { BlocksStructure } from '../../Blocks';
import InstrumentPanel from '../../InstrumentPanel';
import PlayerMenu from '../../PlayerMenu';
import initialSequencerData from '../../sequencerData'; // Переименовываем для ясности
import useEditCells from '../../EditCells';
import audioEngine from '../../AudioEngine';
import VerticalViewOverlay from './VerticalViewOverlay';
import PrerollOverlay from './PrerollOverlay';
import './SequencerModule.css';

const DEFAULT_TIME_SIGNATURE = 4;
const MAX_TIME_SIGNATURE = 6;
// Удаляем прямое использование trackNames и bpm из initialSequencerData здесь, будем брать из состояния или пропсов если нужно
// const trackNames = initialSequencerData.trackNames || []; 
// const initialInstruments = initialSequencerData.initialInstruments || {};
// const bpm = initialSequencerData.bpm || 120;

// Минимальное и максимальное значение BPM
const MIN_BPM = 60;
const MAX_BPM = 240;
const DEFAULT_BPM = 120;

// Внутренний компонент Sequencer
const Sequencer = ({ 
  selectedInstrument, 
  onClearSequencer, 
  onCellsStateChange, 
  currentBarBlocks,
  allBlocks,
  barBlockMapping,
  currentBar,
  onBarChange,
  totalBars,
  playbackPosition,
  cellsState,
  onLoopButtonClick,
  loopedBlockIndices,
  mutedTracks,
  trackNames,
  // Новые пропсы для GRID режима
  isGridModeActive,
  onTrackStepsChange 
}) => {
  const [adjustedPlaybackPosition, setAdjustedPlaybackPosition] = useState(null);
  const { handleCellClick, clearAllCells } = useEditCells(cellsState, onCellsStateChange);

  const sequencerStructure = currentBarBlocks || [];
  
  useEffect(() => {
    // audioEngine.setBPM(bpm); // BPM не передается в Sequencer, управляется из SequencerModule
  }, []);

  useEffect(() => {
    if (onClearSequencer) {
      onClearSequencer(clearAllCells);
    }
  }, [onClearSequencer, clearAllCells]);

  useEffect(() => {
    if (!playbackPosition || !barBlockMapping || !currentBarBlocks) {
      setAdjustedPlaybackPosition(null);
      return;
    }
    const currentBarBlockIndices = barBlockMapping[currentBar] || [];
    if (!currentBarBlockIndices.includes(playbackPosition.blockIndex)) {
      setAdjustedPlaybackPosition(null);
      return;
    }
    const localBlockIndex = currentBarBlocks.findIndex(
      block => block.originalIndex === playbackPosition.blockIndex
    );
    if (localBlockIndex === -1) {
      setAdjustedPlaybackPosition(null);
      return;
    }
    setAdjustedPlaybackPosition({
      blockIndex: localBlockIndex,
      stepIndex: playbackPosition.stepIndex
    });
  }, [playbackPosition, barBlockMapping, currentBar, currentBarBlocks]);

  const getOriginalBlockIndex = (visualBlockIndex) => {
    // allBlocks (переименованный sequencerStructure из пропсов) должен содержать оригинальные индексы
    // или currentBarBlocks, если мы ищем индекс внутри текущего такта.
    // Эта логика может потребовать уточнения в зависимости от того, как используется.
    // Если sequencerStructure здесь - это currentBarBlocks, то:
    if (sequencerStructure[visualBlockIndex] && 
        sequencerStructure[visualBlockIndex].originalIndex !== undefined) {
      return sequencerStructure[visualBlockIndex].originalIndex;
    }
    // Если allBlocks содержит всю структуру, и visualBlockIndex - это индекс в allBlocks
    // if (allBlocks[visualBlockIndex] && allBlocks[visualBlockIndex].originalIndex !== undefined) { ... }
    return visualBlockIndex; // Возврат по умолчанию, если нет originalIndex
  };

  const getCellState = (cellId) => {
    const [blockIndexStr, trackName, stepIndexStr] = cellId.split('-');
    const visualBlockIndex = parseInt(blockIndexStr, 10);
    // Здесь важно: blockIndex в cellId от BlocksStructure - это визуальный индекс в currentBarBlocks.
    // Нам нужно получить оригинальный глобальный индекс блока.
    const originalBlockIndex = getOriginalBlockIndex(visualBlockIndex); 
    const originalCellId = `${originalBlockIndex}-${trackName}-${stepIndexStr}`;
    return cellsState[originalCellId] || { instrumentId: null };
  };

  const handleCellClickWithOriginalIndex = (cellId) => {
    const [blockIndexStr, trackName, stepIndexStr] = cellId.split('-');
    const visualBlockIndex = parseInt(blockIndexStr, 10);
    const originalBlockIndex = getOriginalBlockIndex(visualBlockIndex);
    const originalCellId = `${originalBlockIndex}-${trackName}-${stepIndexStr}`;
    handleCellClick(originalCellId, selectedInstrument);
  };

  return (
    <div className="sequencer-wrapper">
      <BlocksStructure
        sequencerStructure={sequencerStructure}
        trackNames={trackNames}
        getCellState={getCellState}
        handleCellClick={handleCellClickWithOriginalIndex}
        currentPlaybackPosition={adjustedPlaybackPosition}
        onLoopButtonClick={onLoopButtonClick}
        loopedBlockIndices={loopedBlockIndices}
        mutedTracks={mutedTracks}
        // Пропсы для GRID режима в BlocksStructure
        isGridModeActive={isGridModeActive}
        onTrackStepsChange={onTrackStepsChange}
      />
    </div>
  );
};

// Основной компонент SequencerModule
const SequencerModule = forwardRef(({ initialSessionData }, ref) => {
  // Состояние для отслеживания выбранного инструмента
  const [selectedInstrument, setSelectedInstrument] = useState(null);
  
  // Состояние для отслеживания текущего такта и общего количества тактов
  const [currentBar, setCurrentBar] = useState(0);
  const [totalBars, setTotalBars] = useState(1);
  
  // Состояние для хранения соответствия тактов и блоков
  const [barBlockMapping, setBarBlockMapping] = useState({});
  
  // Состояние для отслеживания позиции воспроизведения
  const [playbackPosition, setPlaybackPosition] = useState(null);
  
  // Состояние для хранения данных о ячейках секвенсора
  const [cellsState, setCellsState] = useState({});
  
  // Состояние для отслеживания режима отображения (горизонтальный/вертикальный)
  const [displayMode, setDisplayMode] = useState('horizontal');
  
  // Ref для очистки секвенсора
  const clearCallback = useRef(null);
  
  // Ref для отслеживания свайпов на мобильных устройствах
  const startTouchX = useRef(null);
  
  // Ref для контейнера модуля
  const containerRef = useRef(null);

  // Централизованное состояние для PlayerMenu
  const [isPlaying, setIsPlaying] = useState(false);
  const [bpm, setBpm] = useState(initialSequencerData.bpm || DEFAULT_BPM);
  const [isInitialized, setIsInitialized] = useState(false);
  const [isPrerollEnabled, setIsPrerollEnabled] = useState(true);
  const [isTransitioning, setIsTransitioning] = useState(false);
  
  // trackNames теперь в состоянии
  const [trackNames, setTrackNames] = useState([]);
  
  const [loopedBlockIndices, setLoopedBlockIndices] = useState([]);
  const [mutedTracks, setMutedTracks] = useState(new Set());
  
  // Новое состояние для метронома
  const [isMetronomeEnabled, setIsMetronomeEnabled] = useState(false);

  // Новое состояние для режима управления сеткой
  const [isGridModeActive, setIsGridModeActive] = useState(false);

  // --- НОВЫЕ СОСТОЯНИЯ ДЛЯ СТРУКТУРЫ СЕКВЕНСОРА ---
  const [currentSequencerStructure, setCurrentSequencerStructure] = useState([]);
  const [currentBars, setCurrentBars] = useState([]);
  // --- КОНЕЦ НОВЫХ СОСТОЯНИЙ ---
  
  useImperativeHandle(ref, () => ({
    exportData: (sessionName, folderName) => {
      const dataToExport = {
        version: "1.0",
        sessionName: sessionName,
        folderName: folderName || null,
        bpm,
        trackNames,
        currentSequencerStructure,
        currentBars,
        cellsState,
        loopedBlockIndices,
        mutedTracks: Array.from(mutedTracks), // Set нужно преобразовать в массив
        isMetronomeEnabled,
      };

      const serverUrl = 'https://rhythmcapsule.ru/api/sequencer/save';

      fetch(serverUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(dataToExport),
      })
      .then(response => {
        if (!response.ok) {
          // Попытка получить тело ошибки как JSON, иначе выбросить общую ошибку
          return response.json().then(errData => {
            throw new Error(errData.message || `Ошибка сервера: ${response.status}`);
          }).catch(() => { // Если response.json() не удается (например, ошибка не в JSON)
            throw new Error(`Ошибка сервера: ${response.status} ${response.statusText}`);
          });
        }
        return response.json();
      })
      .then(data => {
        console.log('Данные успешно сохранены на сервере:', data);
        alert(`Сессия '${sessionName}' ${folderName ? 'в папке \'' + folderName + '\'' : ''} успешно сохранена! ID: ${data.sessionId || 'N/A'}`);
      })
      .catch(error => {
        console.error('Ошибка при сохранении данных на сервер:', error);
        alert(`Ошибка сохранения сессии '${sessionName}' на сервер: ${error.message}`);
      });
    }
  }));
  
  // Обновление BPM в аудио движке при изменении BPM и после инициализации движка
  useEffect(() => {
    if (isInitialized) {
      audioEngine.setBPM(bpm);
      console.log(`[BPM Effect] BPM set in audioEngine: ${bpm}`);
    }
  }, [bpm, isInitialized]);

  // Инициализация аудио движка и начальных данных секвенсора
  useEffect(() => {
    const initAudioAndSequencerData = async () => {
      console.log("[Init Effect] Initializing audioEngine...");
      await audioEngine.initialize();
      setIsInitialized(true);
      console.log("[Init Effect] AudioEngine initialized.");

      if (initialSessionData) {
        console.log("[Init Effect] Initializing with provided session data:", initialSessionData);
        // Используем данные из initialSessionData
        setBpm(initialSessionData.bpm || DEFAULT_BPM);
        setTrackNames(initialSessionData.trackNames || []);
        setCurrentSequencerStructure(initialSessionData.currentSequencerStructure || []);
        setCurrentBars(initialSessionData.currentBars || []);
        setCellsState(initialSessionData.cellsState || {});
        setLoopedBlockIndices(initialSessionData.loopedBlockIndices || []);
        setMutedTracks(new Set(initialSessionData.mutedTracks || []));
        setIsMetronomeEnabled(initialSessionData.isMetronomeEnabled || false);
        
        // barBlockMapping и totalBars будут пересчитаны в последующих useEffect
        // так как currentSequencerStructure и currentBars обновлены.
        // Однако, если initialSessionData.barBlockMapping существует и вы ему доверяете, можно установить его напрямую
        // и totalBars соответственно.
        // Для простоты пока оставим автоматический пересчет.

      } else {
        console.log("[Init Effect] Initializing with default initialSequencerData.");
        // Используем данные из initialSequencerData по умолчанию
        const initialStructure = initialSequencerData.sequencerStructure || [];
        const initialBarsConfig = initialSequencerData.bars || [];
        const initialTNames = initialSequencerData.trackNames || [];

        setCurrentSequencerStructure(initialStructure);
        setCurrentBars(initialBarsConfig);
        setTrackNames(initialTNames);

        const mapping = getBarBlockMapping(initialStructure, initialBarsConfig); // Пересчитываем здесь
        setBarBlockMapping(mapping); // Устанавливаем сразу
        const barsCount = Object.keys(mapping).length;
        setTotalBars(barsCount > 0 ? barsCount : 1);

        const initialCells = {};
        if (Array.isArray(initialStructure)) {
          initialStructure.forEach((block, blockIndex) => {
            if (block && block.trackSteps) {
              if (Array.isArray(initialTNames)) {
                initialTNames.forEach((trackName) => {
                  const stepCount = block.trackSteps[trackName];
                  if (stepCount && typeof stepCount === 'number') {
                    for (let i = 0; i < stepCount; i++) {
                      const cellId = `${blockIndex}-${trackName}-${i}`;
                      initialCells[cellId] = { instrumentId: null };
                    }
                  }
                });
              }
            }
          });
        }
        const initialInstrumentsData = initialSequencerData.initialInstruments || {};
        if (typeof initialInstrumentsData === 'object') {
          Object.entries(initialInstrumentsData).forEach(([rowKey, cells]) => {
            if (cells && typeof cells === 'object') {
              Object.entries(cells).forEach(([cellIndex, instrumentId]) => {
                const cellId = `${rowKey}-${cellIndex}`;
                if (initialCells[cellId]) {
                  initialCells[cellId].instrumentId = instrumentId;
                }
              });
            }
          });
        }
        setCellsState(initialCells);
        setBpm(initialSequencerData.bpm || DEFAULT_BPM); // Установка BPM по умолчанию
        // Остальные состояния (loopedBlockIndices, mutedTracks, isMetronomeEnabled) по умолчанию
        setLoopedBlockIndices([]);
        setMutedTracks(new Set());
        setIsMetronomeEnabled(false);
      }
      console.log("Sequencer Module Initialized."); // Изменено сообщение
    };
    initAudioAndSequencerData();
  }, [initialSessionData]); // Добавляем initialSessionData в зависимости useEffect

  // Обновление зависимых данных (например, barBlockMapping), когда currentBars или currentSequencerStructure изменяются
  useEffect(() => {
    if (currentSequencerStructure.length > 0 || currentBars.length > 0) {
      console.log("[Effect] currentSequencerStructure or currentBars changed, recalculating barBlockMapping.");
      const newMapping = getBarBlockMapping(currentSequencerStructure, currentBars);
      setBarBlockMapping(newMapping);
      const newTotalBars = Object.keys(newMapping).length;
      setTotalBars(newTotalBars > 0 ? newTotalBars : 1);
    }
  }, [currentSequencerStructure, currentBars]);

  // useEffect для audioEngine.prepareSequencer
  useEffect(() => {
    if (isInitialized && currentSequencerStructure.length > 0 && trackNames.length > 0 && Object.keys(cellsState).length > 0 && Object.keys(barBlockMapping).length > 0) {
      console.log('[Effect prepareSequencer] Dependencies met. Calling prepare.');
      audioEngine.prepareSequencer(currentSequencerStructure, trackNames, cellsState, barBlockMapping);
    } else {
      // console.log('[Effect prepareSequencer] Skipping prepare - dependencies not ready.');
    }
  }, [isInitialized, currentSequencerStructure, trackNames, cellsState, barBlockMapping]);

  // Функция для вычисления barBlockMapping (НЕ принимает trackNames)
  const getBarBlockMapping = (structure, barsConfig) => {
    const mapping = {};
    let currentBlockIndex = 0;
    const predefinedBars = barsConfig || [];
    const totalBlocksInStructure = structure.length;

    predefinedBars.forEach((bar, barIndex) => {
      mapping[barIndex] = [];
      const blocksInThisBar = bar.timeSignature || DEFAULT_TIME_SIGNATURE;
      for (let i = 0; i < blocksInThisBar; i++) {
        if (currentBlockIndex < totalBlocksInStructure) {
          mapping[barIndex].push(currentBlockIndex);
          currentBlockIndex++;
        }
      }
    });

    let additionalBarIndex = predefinedBars.length;
    while (currentBlockIndex < totalBlocksInStructure) {
      mapping[additionalBarIndex] = [];
      const remainingBlocks = totalBlocksInStructure - currentBlockIndex;
      const barSize = Math.min(remainingBlocks, MAX_TIME_SIGNATURE);
      for (let i = 0; i < barSize; i++) {
        if (currentBlockIndex < totalBlocksInStructure) {
          mapping[additionalBarIndex].push(currentBlockIndex);
          currentBlockIndex++;
        }
      }
      additionalBarIndex++;
    }
    return mapping;
  };
  
  // Вывод информации о количестве тактов и блоков (без изменений, но зависимости важны)
  useEffect(() => {
    if (totalBars > 0 && currentSequencerStructure.length > 0 && Object.keys(barBlockMapping).length > 0) {
        console.log(`[Log State] ${totalBars} тактов, ${currentSequencerStructure.length} блоков.`);
    } 
  }, [totalBars, currentSequencerStructure, barBlockMapping]);

  // getCurrentBarInfo (использует currentBars)
  const getCurrentBarInfo = () => {
    const predefinedBars = currentBars || [];
    if (predefinedBars[currentBar]) {
      return predefinedBars[currentBar];
    }
    return {
      id: currentBar + 1,
      name: `Такт ${currentBar + 1}`,
      timeSignature: (barBlockMapping[currentBar] || []).length || DEFAULT_TIME_SIGNATURE
    };
  };
  
  // getCurrentBarBlocks (использует currentSequencerStructure и barBlockMapping)
  const getCurrentBarBlocks = () => {
    if (!currentSequencerStructure || currentSequencerStructure.length === 0 || Object.keys(barBlockMapping).length === 0) return [];
    const blockIndices = barBlockMapping[currentBar] || [];
    return blockIndices.map(index => {
      if (currentSequencerStructure[index]) {
        return {
          ...currentSequencerStructure[index],
          originalIndex: index
        };
      }
      return null;
    }).filter(Boolean);
  };

  const handleInstrumentSelect = (instrumentId, instrumentConfig) => {
    console.log('Выбран инструмент:', instrumentId);
    console.log('Конфигурация:', instrumentConfig);
    setSelectedInstrument(instrumentConfig ? { id: instrumentId, config: instrumentConfig } : null);
  };

  const handleCellClick = (cellId) => {
    // Определяем, имеет ли Sequencer доступ к handleCellClickWithOriginalIndex
    if (clearCallback.current) {
      // Используем внутренний handleCellClick через callback
      // Этот подход требует модификации внутреннего компонента Sequencer для экспорта handleCellClickWithOriginalIndex
      console.log('Клик по ячейке в вертикальном режиме:', cellId);
    }
  };

  const handleClearAll = () => {
    if (window.confirm('Вы уверены, что хотите очистить все ячейки?')) {
      if (clearCallback.current) {
        clearCallback.current();
      }
    }
  };

  // Обработчики свайпа для переключения между тактами
  const handleTouchStart = (e) => {
    startTouchX.current = e.touches[0].clientX;
  };

  const handleTouchEnd = (e) => {
    if (!startTouchX.current) return;
    
    const endTouchX = e.changedTouches[0].clientX;
    const diffX = endTouchX - startTouchX.current;
    
    // Свайп вправо - предыдущий такт
    if (diffX > 75 && currentBar > 0) {
      setCurrentBar(currentBar - 1);
    }
    // Свайп влево - следующий такт
    else if (diffX < -75 && currentBar < totalBars - 1) {
      setCurrentBar(currentBar + 1);
    }
    
    startTouchX.current = null;
  };

  // Установка обработчика для обновления позиции воспроизведения
  useEffect(() => {
    const handlePlaybackPosition = (position) => {
      setPlaybackPosition(position);
      
      // Если position равен null, значит воспроизведение остановлено
      if (!position) return;
      
      // Автоматическое переключение между тактами при воспроизведении
      if (barBlockMapping && totalBars > 0) {
        // Определяем, к какому такту относится текущая позиция воспроизведения
        for (let barIndex = 0; barIndex < totalBars; barIndex++) {
          const blockIndices = barBlockMapping[barIndex] || [];
          if (blockIndices.includes(position.blockIndex)) {
            // Если позиция воспроизведения перешла в другой такт, обновляем UI
            if (barIndex !== currentBar) {
              setCurrentBar(barIndex);
            }
            break;
          }
        }
      }
    };
    
    audioEngine.setPositionCallback(handlePlaybackPosition);
    
    // Очистка при размонтировании
    return () => {
      audioEngine.setPositionCallback(null);
    };
  }, [barBlockMapping, currentBar, totalBars]);

  // Обработчик переключения воспроизведения
  const handlePlayToggle = async () => {
    if (!isInitialized || isTransitioning) return;
    
    setIsTransitioning(true);

    if (!isPlaying) {
      if (isPrerollEnabled) {
        // Добавляем обработчик отмены преролла для сброса флага isTransitioning
        const onPrerollCancel = () => {
          setIsTransitioning(false);
        };
        
        // Регистрируем обработчик отмены преролла
        audioEngine.setPrerollCancelCallback(onPrerollCancel);
        
        audioEngine.startPreroll(getCurrentBarInfo().timeSignature, async () => {
          try {
            // Больше не переключаемся автоматически на вертикальный режим
            await audioEngine.play();
            setIsPlaying(true);
          } catch (error) {
            console.error("Error during playback after preroll:", error);
            setIsPlaying(false);
          } finally {
            setIsTransitioning(false);
            // Удаляем обработчик отмены преролла
            audioEngine.setPrerollCancelCallback(null);
          }
        });
        return; 
      }
      
      // Если preroll отключен, сразу начинаем воспроизведение, но не меняем режим
      try {
        await audioEngine.play();
        setIsPlaying(true);
      } catch (error) {
        console.error("Error during playback without preroll:", error);
        setIsPlaying(false);
      } finally {
        setIsTransitioning(false);
      }
    } else {
      // При остановке воспроизведения не переключаемся в горизонтальный режим
      try {
        await audioEngine.stop();
        setIsPlaying(false);
      } catch (error) {
        console.error("Error during stop:", error);
      } finally {
        setIsTransitioning(false);
      }
    }
  };

  // Обработчик изменения режима отображения
  const handleVerticalModeChange = (mode) => {
    setDisplayMode(mode);
  };

  // Получаем обрабочтик для клика по ячейке в вертикальном режиме
  const getHandleVerticalCellClick = () => {
    return (cellId) => {
      // Если выбран инструмент, добавляем его в ячейку
      if (selectedInstrument) {
        setCellsState(prev => {
          const newState = { ...prev };
          newState[cellId] = {
            instrumentId: selectedInstrument.id
          };
          return newState;
        });
        
        console.log(`Установлен инструмент ${selectedInstrument.id} в ячейку ${cellId}`);
      } else {
        // Если инструмент не выбран, очищаем ячейку
        setCellsState(prev => {
          const newState = { ...prev };
          if (newState[cellId]) {
            newState[cellId] = { instrumentId: null };
          }
          return newState;
        });
        
        console.log(`Ячейка ${cellId} очищена`);
      }
    };
  };

  // Обработчик клика по кнопке LOOP (новая логика для списка)
  const handleLoopButtonClick = (clickedOriginalBlockIndex) => {
    let newLoopedIndices = [...loopedBlockIndices];
    const existingIndexPos = newLoopedIndices.indexOf(clickedOriginalBlockIndex);
    if (existingIndexPos > -1) newLoopedIndices.splice(existingIndexPos, 1);
    else { newLoopedIndices.push(clickedOriginalBlockIndex); newLoopedIndices.sort((a, b) => a - b); }
    setLoopedBlockIndices(newLoopedIndices);
    if (newLoopedIndices.length > 0) audioEngine.setLoopedBlocks(newLoopedIndices);
    else audioEngine.clearLoopedBlocks();

    if (isInitialized && currentSequencerStructure.length > 0 && trackNames.length > 0 && Object.keys(cellsState).length > 0) {
      const wasPlaying = isPlaying;
      let capturedTime = 0;
      if (wasPlaying) capturedTime = audioEngine.getCurrentTransportTime();
      audioEngine.prepareSequencer(currentSequencerStructure, trackNames, cellsState, barBlockMapping);
      if (wasPlaying) audioEngine.play(capturedTime).catch(err => setIsPlaying(false));
    }
  };

  // Обработчик для переключения мьютирования трека - ТОЛЬКО ОБНОВЛЕНИЕ СОСТОЯНИЯ
  const handleMuteToggle = useCallback((trackName) => {
    setMutedTracks(prev => {
      const newSet = new Set(prev);
      if (newSet.has(trackName)) {
        newSet.delete(trackName);
        console.log(`Track ${trackName} unmuted.`);
      } else {
        newSet.add(trackName);
        console.log(`Track ${trackName} muted.`);
      }
      // // Обновляем состояние в AudioEngine - УБРАНО ОТСЮДА
      // audioEngine.setMutedTracks(newSet);
      return newSet;
    });

    // // Переподготавливаем секвенсор - УБРАНО ОТСЮДА
    // if (isInitialized && sequencerData.sequencerStructure && trackNames && cellsState) { ... }

  }, []); // Зависимости не нужны, т.к. используем только setMutedTracks и prev

  // Эффект для синхронизации AudioEngine с состоянием mutedTracks
  useEffect(() => {
    if (!isInitialized) return;
    audioEngine.setMutedTracks(mutedTracks);
    if (currentSequencerStructure.length > 0 && trackNames.length > 0 && Object.keys(cellsState).length > 0) {
      const wasPlaying = isPlaying;
      let capturedTime = 0;
      if (wasPlaying) { capturedTime = audioEngine.getCurrentTransportTime(); audioEngine.stop(); }
      audioEngine.prepareSequencer(currentSequencerStructure, trackNames, cellsState, barBlockMapping);
      if (wasPlaying) audioEngine.play(capturedTime).catch(err => console.error("Error restarting after mute:", err));
    }
  }, [mutedTracks, isInitialized, currentSequencerStructure, trackNames, cellsState, barBlockMapping, isPlaying]);

  // Обработчик для переключения метронома
  const handleMetronomeToggle = useCallback(() => {
      const newState = !isMetronomeEnabled;
      setIsMetronomeEnabled(newState);
      if (isInitialized) {
           // Вызываем метод AudioEngine для обновления состояния и переподготовки, если нужно
           audioEngine.setMetronomeEnabled(newState);
      }
  }, [isMetronomeEnabled, isInitialized]); // Добавляем зависимости

  // Обновление данных секвенсора при изменении ячеек (убираем isPlaying и mutedTracks, они обрабатываются отдельно)
  // Этот useEffect теперь отвечает только за базовую подготовку при изменениях структуры/ячеек
  useEffect(() => {
    if (isInitialized && currentSequencerStructure && trackNames && cellsState && barBlockMapping) {
       console.log('[SequencerModule] Calling prepareSequencer due to cellsState/trackNames/structure/mapping change.');
       // Передаем barBlockMapping в prepareSequencer
      audioEngine.prepareSequencer(currentSequencerStructure, trackNames, cellsState, barBlockMapping);
    }
  }, [isInitialized, trackNames, cellsState, barBlockMapping]); // Добавили barBlockMapping

  // Обработчик переключения режима управления сеткой
  const handleToggleGridMode = () => {
    setIsGridModeActive(prev => !prev);
  };

  // Эта функция остается для изменения КОЛИЧЕСТВА БЛОКОВ В ТЕКУЩЕМ ТАКТЕ
  const handleTimeSignatureChange = (newBlockCountInBar) => {
    console.log(`[Grid Mode] Block count change for bar ${currentBar} to: ${newBlockCountInBar}`);
    if (newBlockCountInBar < 1 || newBlockCountInBar > MAX_TIME_SIGNATURE) {
      console.warn(`Invalid block count: ${newBlockCountInBar}`);
      return;
    }

    const originalStructure = [...currentSequencerStructure];
    const originalBars = currentBars ? JSON.parse(JSON.stringify(currentBars)) : [];
    const originalCells = {...cellsState};

    let newStructure = [...originalStructure];
    let newBars = [...originalBars];
    
    if (!newBars[currentBar]) {
      console.error(`Current bar ${currentBar} not found!`);
      // Аварийное создание такта, если его нет
      newBars[currentBar] = { id: (newBars.length > 0 ? Math.max(...newBars.map(b => b.id)) : 0) + 1 , name: `Такт ${currentBar + 1}`, timeSignature: DEFAULT_TIME_SIGNATURE };
    }
    
    const oldBlockCountInBar = newBars[currentBar].timeSignature;
    const diff = newBlockCountInBar - oldBlockCountInBar;

    if (diff === 0) return;

    let firstBlockGlobalIndex = 0;
    for (let i = 0; i < currentBar; i++) {
      firstBlockGlobalIndex += newBars[i]?.timeSignature || 0;
    }
    const modificationPointGlobalIndex = firstBlockGlobalIndex + oldBlockCountInBar;
    
    newBars[currentBar].timeSignature = newBlockCountInBar;
    let blockIndexOffset = diff; // На сколько сместятся индексы блоков ПОСЛЕ точки изменения

    if (diff > 0) { // Добавляем блоки
      for (let i = 0; i < diff; i++) {
        const newBlockId = "block-" + Date.now() + Math.random().toString(36).substring(2, 7);
        const newBlock = {
          id: newBlockId,
          name: `Блок ${originalStructure.length + 1 + i}`,
          trackSteps: { ...trackNames.reduce((acc, name) => ({ ...acc, [name]: 4 }), {}) }, 
        };
        newStructure.splice(modificationPointGlobalIndex + i, 0, newBlock);
      }
    } else { // Удаляем блоки (diff < 0)
      const numToRemove = Math.abs(diff);
      newStructure.splice(modificationPointGlobalIndex - numToRemove, numToRemove);
    }

    // --- Перемаппинг cellsState ---
    const remappedCells = {};
    // Сохраняем уникальные ID блоков из originalStructure для поиска старых ячеек
    const originalBlockIdMap = {}; 
    originalStructure.forEach((block, index) => {
        if(block && block.id) originalBlockIdMap[block.id] = index; // map id to old global index
    });

    newStructure.forEach((currentBlock, newGlobalIndex) => {
      trackNames.forEach(trackName => {
        const numSteps = currentBlock.trackSteps[trackName] || 0;
        for (let stepIndex = 0; stepIndex < numSteps; stepIndex++) {
          const newCellId = `${newGlobalIndex}-${trackName}-${stepIndex}`;
          let cellValue = { instrumentId: null }; // Значение по умолчанию для новых ячеек

          // Пытаемся найти старое значение, если этот блок существовал ранее
          if (currentBlock.id && originalBlockIdMap.hasOwnProperty(currentBlock.id)) {
            const oldGlobalIndex = originalBlockIdMap[currentBlock.id];
            const oldCellId = `${oldGlobalIndex}-${trackName}-${stepIndex}`;
            if (originalCells.hasOwnProperty(oldCellId)) {
              cellValue = originalCells[oldCellId];
            }
          }
          // Если блок совершенно новый (не было такого ID), он уже получит cellValue по умолчанию.
          remappedCells[newCellId] = cellValue;
        }
      });
    });

    setCurrentSequencerStructure(newStructure);
    setCurrentBars(newBars);
    setCellsState(remappedCells); // Устанавливаем полностью перестроенный cellsState

    console.log("[Grid Mode] States updated by handleTimeSignatureChange with cell remapping.");
  };
  
  const handleAddBar = () => {
    console.log("[Grid Mode] Add BAR requested after current bar:", currentBar);
    
    const originalStructure = [...currentSequencerStructure];
    const originalBars = currentBars ? JSON.parse(JSON.stringify(currentBars)) : [];
    const originalCells = {...cellsState};

    let newStructure = [...originalStructure];
    let newBars = [...originalBars];

    const currentBarConfig = newBars[currentBar];
    let newBarIndex;
    let newBarSize;

    if (!currentBarConfig && newBars.length === 0) { // Случай добавления первого такта
        console.log("[AddBar] Adding the very first bar.");
        newBarSize = DEFAULT_TIME_SIGNATURE;
        const newBarObject = { id: 1, name: `Такт 1`, timeSignature: newBarSize };
        newBars.push(newBarObject);
        newBarIndex = 0; // Индекс нового (первого) такта
    } else if (currentBarConfig) { // Добавляем после существующего такта
        newBarSize = currentBarConfig.timeSignature;
        newBarIndex = currentBar + 1;
        const newBarObject = { 
            id: (newBars.length > 0 ? Math.max(...newBars.map(b => b.id)) : 0) + 1,
            name: `Такт ${newBars.length + 1}`,
            timeSignature: newBarSize 
        };
        newBars.splice(newBarIndex, 0, newBarObject);
    } else {
        console.error("[AddBar] Cannot add bar - current bar config not found and it's not the first bar.");
        return;
    }

    let insertionPointGlobalBlockIndex = 0;
    for (let i = 0; i < newBarIndex; i++) {
        insertionPointGlobalBlockIndex += newBars[i].timeSignature; 
    }

    for (let i = 0; i < newBarSize; i++) {
        const newBlockId = "block-" + Date.now() + i + Math.random().toString(36).substring(2,7);
        const newBlock = {
            id: newBlockId,
            name: `Блок ${originalStructure.length + 1 + i}`,
            trackSteps: { ...trackNames.reduce((acc, name) => ({ ...acc, [name]: 4 }), {}) }, 
        };
        newStructure.splice(insertionPointGlobalBlockIndex + i, 0, newBlock);
    }
    console.log(`[AddBar] Added new bar at index ${newBarIndex} with ${newBarSize} blocks.`);

    // --- Перемаппинг cellsState ---
    const remappedCells = {};
    const originalBlockIdMap = {};
    originalStructure.forEach((block, index) => {
        if(block && block.id) originalBlockIdMap[block.id] = index;
    });

    newStructure.forEach((currentBlock, newGlobalIndex) => {
      trackNames.forEach(trackName => {
        const numSteps = currentBlock.trackSteps[trackName] || 0;
        for (let stepIndex = 0; stepIndex < numSteps; stepIndex++) {
          const newCellId = `${newGlobalIndex}-${trackName}-${stepIndex}`;
          let cellValue = { instrumentId: null };
          if (currentBlock.id && originalBlockIdMap.hasOwnProperty(currentBlock.id)) {
            const oldGlobalIndex = originalBlockIdMap[currentBlock.id];
            const oldCellId = `${oldGlobalIndex}-${trackName}-${stepIndex}`;
            if (originalCells.hasOwnProperty(oldCellId)) {
              cellValue = originalCells[oldCellId];
            }
          }
          remappedCells[newCellId] = cellValue;
        }
      });
    });

    setCurrentSequencerStructure(newStructure);
    setCurrentBars(newBars);
    setCellsState(remappedCells);
    setCurrentBar(newBarIndex); 
  };

  const handleRemoveBar = () => {
    console.log("[Grid Mode] Remove CURRENT bar requested:", currentBar);
    if (currentBars.length <= 1) {
        console.warn("[RemoveBar] Cannot remove the last bar.");
        return;
    }

    const originalStructure = [...currentSequencerStructure];
    const originalBars = JSON.parse(JSON.stringify(currentBars)); // Deep copy for safety before splice
    const originalCells = {...cellsState};

    let newStructure = [...originalStructure];
    let newBars = [...originalBars]; // Will be mutated by splice

    const barToRemoveConfig = newBars[currentBar];
    if (!barToRemoveConfig) {
        console.error("[RemoveBar] Config for bar to remove not found!");
        return;
    }
    const numBlocksInRemovedBar = barToRemoveConfig.timeSignature;

    let firstBlockOfRemovedBarGlobalIndex = 0;
    for (let i = 0; i < currentBar; i++) {
        firstBlockOfRemovedBarGlobalIndex += newBars[i]?.timeSignature || 0;
    }

    newStructure.splice(firstBlockOfRemovedBarGlobalIndex, numBlocksInRemovedBar);
    newBars.splice(currentBar, 1);
    console.log(`[RemoveBar] Removed bar ${currentBar} and its ${numBlocksInRemovedBar} blocks.`);

    // --- Перемаппинг cellsState ---
    const remappedCells = {};
    const originalBlockIdMap = {}; 
    originalStructure.forEach((block, index) => {
        if(block && block.id) originalBlockIdMap[block.id] = index;
    });

    newStructure.forEach((currentBlock, newGlobalIndex) => {
      trackNames.forEach(trackName => {
        const numSteps = currentBlock.trackSteps[trackName] || 0;
        for (let stepIndex = 0; stepIndex < numSteps; stepIndex++) {
          const newCellId = `${newGlobalIndex}-${trackName}-${stepIndex}`;
          let cellValue = { instrumentId: null };
          if (currentBlock.id && originalBlockIdMap.hasOwnProperty(currentBlock.id)) {
            const oldGlobalIndex = originalBlockIdMap[currentBlock.id];
            const oldCellId = `${oldGlobalIndex}-${trackName}-${stepIndex}`;
            if (originalCells.hasOwnProperty(oldCellId)) {
              cellValue = originalCells[oldCellId];
            }
          }
          remappedCells[newCellId] = cellValue;
        }
      });
    });

    setCurrentSequencerStructure(newStructure);
    setCurrentBars(newBars);
    setCellsState(remappedCells);

    if (currentBar >= newBars.length && newBars.length > 0) {
        setCurrentBar(newBars.length - 1);
    } else if (newBars.length === 0) {
        // Этого случая мы избегаем проверкой currentBars.length <= 1 выше
        // Но если бы он был возможен, нужно было бы создать новый такт по умолчанию или setCurrentBar(0)
        // и затем handleAddBar мог бы создать первый такт.
        // Для безопасности, если как-то сюда попали:
        setCurrentBar(0); 
        // Можно было бы инициировать создание первого такта тут, но пока оставляем так.
    }
    // Если currentBar все еще валиден, он остается.
  };

  // НОВАЯ ФУНКЦИЯ для изменения количества шагов в дорожке блока
  const handleTrackStepsChange = (blockId, trackName, newStepCount) => {
    console.log(`[Grid Mode] Track steps change for block ${blockId}, track ${trackName} to: ${newStepCount}`);
    // Добавьте здесь ограничения на newStepCount, если необходимо (например, 1-16)

    const originalStructure = [...currentSequencerStructure];
    const originalCells = {...cellsState};

    let newStructure = originalStructure.map(block => {
      if (block.id === blockId) {
        const newTrackSteps = { ...block.trackSteps, [trackName]: newStepCount };
        return { ...block, trackSteps: newTrackSteps };
      }
      return block;
    });

    // --- Перемаппинг cellsState ---
    const remappedCells = {};
    // Сохраняем уникальные ID блоков из originalStructure для поиска старых ячеек
    const originalBlockIdMap = {};
    originalStructure.forEach((block, index) => {
        if(block && block.id) originalBlockIdMap[block.id] = index; // map id to old global index
    });

    newStructure.forEach((currentBlock, newGlobalIndex) => {
      trackNames.forEach(currentTrackName => { // Используем currentTrackName для ясности
        const numSteps = currentBlock.trackSteps[currentTrackName] || 0;
        for (let stepIndex = 0; stepIndex < numSteps; stepIndex++) {
          const newCellId = `${newGlobalIndex}-${currentTrackName}-${stepIndex}`;
          let cellValue = { instrumentId: null }; // Значение по умолчанию

          // Пытаемся найти старое значение, если этот блок существовал ранее
          // и если эта конкретная дорожка обрабатывается
          if (currentBlock.id && originalBlockIdMap.hasOwnProperty(currentBlock.id)) {
            const oldGlobalIndex = originalBlockIdMap[currentBlock.id];
            // Нам нужно знать старое количество шагов для этой дорожки в этом блоке
            const originalBlockForOldSteps = originalStructure.find(b => b.id === currentBlock.id);
            const oldStepCountForTrack = originalBlockForOldSteps ? (originalBlockForOldSteps.trackSteps[currentTrackName] || 0) : 0;

            if (stepIndex < oldStepCountForTrack) { // Только если текущий stepIndex был в старом диапазоне
              const oldCellId = `${oldGlobalIndex}-${currentTrackName}-${stepIndex}`;
              if (originalCells.hasOwnProperty(oldCellId)) {
                cellValue = originalCells[oldCellId];
              }
            }
          }
          // Если блок совершенно новый (не было такого ID), или stepIndex выходит за пределы старых шагов,
          // он уже получит cellValue по умолчанию.
          remappedCells[newCellId] = cellValue;
        }
      });
    });

    setCurrentSequencerStructure(newStructure);
    setCellsState(remappedCells);
    console.log("[Grid Mode] States updated by handleTrackStepsChange with cell remapping.");
  };

  return (
    <div 
      className={`sequencer-module ${displayMode === 'vertical' ? 'vertical-mode' : ''}`}
      ref={containerRef}
      onTouchStart={handleTouchStart}
      onTouchEnd={handleTouchEnd}
    >
      {/* Обычное отображение секвенсора, только если не в вертикальном режиме */}
      {displayMode === 'horizontal' && (
        <>
          <InstrumentPanel 
            onInstrumentSelect={handleInstrumentSelect}
            onClearAll={handleClearAll}
            // Пропсы для режима GRID
            isGridModeActive={isGridModeActive}
            onToggleGridMode={handleToggleGridMode}
            currentBarInfo={getCurrentBarInfo()} 
            onTimeSignatureChange={handleTimeSignatureChange}
            onAddBar={handleAddBar}
            onRemoveBar={handleRemoveBar}
          />
          
          <Sequencer 
            selectedInstrument={selectedInstrument}
            onClearSequencer={(callback) => {
              clearCallback.current = callback;
            }}
            onCellsStateChange={setCellsState}
            currentBarBlocks={getCurrentBarBlocks()}
            allBlocks={currentSequencerStructure}
            barBlockMapping={barBlockMapping}
            currentBar={currentBar}
            onBarChange={setCurrentBar}
            totalBars={totalBars}
            playbackPosition={playbackPosition}
            cellsState={cellsState}
            onLoopButtonClick={handleLoopButtonClick}
            loopedBlockIndices={loopedBlockIndices}
            mutedTracks={mutedTracks}
            trackNames={trackNames}
            // Пропсы для GRID режима в Sequencer
            isGridModeActive={isGridModeActive}
            onTrackStepsChange={handleTrackStepsChange}
          />
          
          <PlayerMenu 
            sequencerStructure={currentSequencerStructure}
            trackNames={trackNames}
            cellsState={cellsState}
            currentBar={currentBar}
            totalBars={totalBars}
            onBarChange={setCurrentBar}
            currentBarInfo={getCurrentBarInfo()}
            onVerticalModeChange={handleVerticalModeChange}
            inVerticalMode={false}
            // Новые пропсы для общего состояния
            isPlaying={isPlaying}
            bpm={bpm}
            isInitialized={isInitialized}
            isPrerollEnabled={isPrerollEnabled}
            isTransitioning={isTransitioning}
            onPlayToggle={handlePlayToggle}
            onBpmChange={setBpm}
            onPrerollToggle={setIsPrerollEnabled}
            handleMuteToggle={handleMuteToggle}
            // Новые пропсы для метронома
            isMetronomeEnabled={isMetronomeEnabled}
            onMetronomeToggle={handleMetronomeToggle}
          />
        </>
      )}
      
      {/* Отдельное полноэкранное вертикальное отображение */}
      <VerticalViewOverlay
        isActive={displayMode === 'vertical'}
        blocks={currentSequencerStructure}
        trackNames={trackNames}
        cellsState={cellsState}
        currentPlaybackPosition={playbackPosition}
        handleCellClick={getHandleVerticalCellClick()}
        currentBar={currentBar}
        totalBars={totalBars}
        onBarChange={setCurrentBar}
        currentBarInfo={getCurrentBarInfo()}
        sequencerStructure={currentSequencerStructure}
        onVerticalModeChange={handleVerticalModeChange}
        // Новые пропсы для общего состояния
        isPlaying={isPlaying}
        bpm={bpm}
        isInitialized={isInitialized}
        isPrerollEnabled={isPrerollEnabled}
        isTransitioning={isTransitioning}
        onPlayToggle={handlePlayToggle}
        onBpmChange={setBpm}
        onPrerollToggle={setIsPrerollEnabled}
        onLoopButtonClick={handleLoopButtonClick}
        loopedBlockIndices={loopedBlockIndices}
        mutedTracks={mutedTracks}
        handleMuteToggle={handleMuteToggle}
        // Новые пропсы для метронома
        isMetronomeEnabled={isMetronomeEnabled}
        onMetronomeToggle={handleMetronomeToggle}
      />

      {/* Компонент для отображения preroll */}
      <PrerollOverlay />
    </div>
  );
});

export default SequencerModule; 