import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import './App.css';
import SequencerPage from './pages/SequencerPage';
import SavedSessionsPage from './pages/SavedSessionsPage';
import { viewport } from '@telegram-apps/sdk';

function App() {
  const [isFullscreen, setIsFullscreen] = useState(false);

  // Инициализация при запуске приложения
  useEffect(() => {
    // Проверяем доступен ли Telegram WebApp
    const isTelegramAvailable = Boolean(viewport && viewport.mount && typeof viewport.mount === 'function');

    if (isTelegramAvailable) {
      // Монтируем viewport
      try {
        viewport.mount();

        // Привязываем CSS переменные для доступа к размерам viewport
        if (viewport.bindCssVars && typeof viewport.bindCssVars === 'function') {
          viewport.bindCssVars();
        }

        // Расширяем до полного размера
        if (viewport.expand && typeof viewport.expand === 'function') {
          viewport.expand();
        }

        // Автоматически переходим в полноэкранный режим при запуске
        if (viewport.requestFullscreen && typeof viewport.requestFullscreen === 'function') {
          viewport.requestFullscreen()
            .then(() => {
              setIsFullscreen(true);
            })
            .catch(err => {
              console.error('Ошибка при запуске полноэкранного режима', err);
            });
        }
      } catch (error) {
        console.error('Ошибка при инициализации Telegram WebApp', error);
      }
    }

    // Размонтирование при закрытии приложения
    return () => {
      if (viewport && viewport.unmount && typeof viewport.unmount === 'function') {
        try {
          viewport.unmount();
        } catch (error) {
          console.error('Ошибка при размонтировании Telegram WebApp', error);
        }
      }
    };
  }, []);

  // Функция для переключения полноэкранного режима
  const toggleFullscreen = async () => {
    try {
      if (isFullscreen) {
        // Выходим из полноэкранного режима
        if (viewport.exitFullscreen && typeof viewport.exitFullscreen === 'function') {
          await viewport.exitFullscreen();
          setIsFullscreen(false);
        }
      } else {
        // Входим в полноэкранный режим
        if (viewport.requestFullscreen && typeof viewport.requestFullscreen === 'function') {
          await viewport.requestFullscreen();
          setIsFullscreen(true);
        }
      }
    } catch (error) {
      console.error('Ошибка при переключении полноэкранного режима', error);
    }
  };

  // Проверка доступны ли методы Telegram WebApp
  const isTelegramAvailable = Boolean(viewport && viewport.requestFullscreen);

  return (
    <Router>
      <div className="App">
        {/* Показываем кнопку только если методы Telegram доступны */}
        {isTelegramAvailable && (
          <button 
            onClick={toggleFullscreen} 
            className="fullscreen-button"
          >
            {isFullscreen ? 'Выйти из полноэкрана' : 'Полный экран'}
          </button>
        )}

        <nav>
          <Link to="/">Главная</Link>
          <Link to="/sequencer">Секвенсор</Link>
          <Link to="/savedsessions">Сохраненные сессии</Link>
        </nav>

        <Routes>
          <Route path="/" element={
            <div className="cover-container">
              <img 
                src="cover.PNG" 
                alt="Обложка" 
                className="cover-image" 
              />
            </div>
          } />
          <Route path="/sequencer" element={<SequencerPage />} />
          <Route path="/savedsessions" element={<SavedSessionsPage />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
