import * as Tone from 'tone';
import instruments from './instruments';

class AudioEngine {
  constructor() {
    this.isPlaying = false;
    this.bpm = 120; // BPM по умолчанию
    this.transport = Tone.Transport;
    this.sequences = [];
    this.instruments = {};
    this.initialized = false;
    this.playbackPosition = { blockIndex: 0, stepIndex: 0 };
    this.totalDuration = 0;
    this.onPositionChange = null;
    this.onPrerollStart = null;
    this.onPrerollBeat = null;
    this.onPrerollCancel = null;
    this.loopedOriginalIndices = [];
    this.mutedTracks = new Set(); // Для хранения замьютированных треков

    // Состояние метронома
    this.isMetronomeEnabled = false;
    this.metronomeSynth = null;
    this.accentMetronomeSynth = null;

    // Состояние преролла
    this.prerollSynths = null;
    this.prerollTimeouts = [];
    this.isPrerollActive = false;

    // Хранение текущих данных для переподготовки
    this.currentSequencerStructure = null;
    this.currentTrackNames = null;
    this.currentCellsState = null;
    this.currentBarBlockMapping = null; // Добавляем хранение barBlockMapping

    // Дополнительные свойства для управления эффективной последовательностью
    this.effectiveSequencerStructure = []; // Блоки в порядке их проигрывания в текущем лупе/полной секвенции
    this.originalIndexMapping = [];      // Карта для преобразования tempBlockIndex -> originalBlockIndex

    // Устанавливаем BPM по умолчанию для Tone.Transport при инициализации
    this.transport.bpm.value = this.bpm;
  }

  // Инициализация инструментов
  async initialize() {
    if (this.initialized) return true;
    
    const loadPromises = Object.entries(instruments).map(([instrumentId, config]) => {
      return new Promise(resolve => {
        const urls = config.urls || { C4: "default.mp3" };
        const baseUrl = config.baseUrl || "./samples/";
        
        const sampler = new Tone.Sampler({
          urls,
          baseUrl,
          onload: () => {
            if (typeof config.volume === 'number') {
              sampler.volume.value = config.volume;
            }
            this.instruments[instrumentId] = sampler;
            resolve();
          }
        }).toDestination();
      });
    });

    await Promise.all(loadPromises);
    this.initialized = true;
    return true;
  }

  // Получение инструмента
  getInstrument(instrumentId) {
    return this.instruments[instrumentId] || null;
  }

  // Установка BPM
  setBPM(bpm) {
    this.bpm = bpm;
    this.transport.bpm.value = bpm; // Убедимся, что транспорт Tone.js использует актуальный BPM
  }

  // Новые методы для управления списком зацикленных блоков
  setLoopedBlocks(indices) {
    // Просто сохраняем переданный массив индексов
    this.loopedOriginalIndices = Array.isArray(indices) ? [...indices] : [];
    console.log("AudioEngine: Looped blocks set to indices:", this.loopedOriginalIndices);
    // Важно: после вызова этого метода нужно будет вызвать prepareSequencer,
    // чтобы изменения применились к транспорту.
  }

  clearLoopedBlocks() {
    this.loopedOriginalIndices = [];
    console.log("AudioEngine: Looped blocks cleared.");
    // Важно: после вызова этого метода нужно будет вызвать prepareSequencer,
    // чтобы изменения применились к транспорту.
  }

  // Метод для установки замьютированных треков
  setMutedTracks(mutedTracksSet) {
    this.mutedTracks = new Set(mutedTracksSet);
    console.log("AudioEngine: Muted tracks updated:", Array.from(this.mutedTracks));
    // Примечание: prepareSequencer должен быть вызван отдельно для применения изменений
  }

  // Установка колбэка для обновления позиции воспроизведения
  setPositionCallback(callback) {
    this.onPositionChange = callback;
  }

  // Установка колбэка для обработчика начала preroll
  setPrerollStartCallback(callback) {
    this.onPrerollStart = callback;
  }

  // Установка колбэка для обработчика удара preroll
  setPrerollBeatCallback(callback) {
    this.onPrerollBeat = callback;
  }

  // Установка колбэка для обработчика отмены преролла
  setPrerollCancelCallback(callback) {
    this.onPrerollCancel = callback;
  }

  // Включение/выключение метронома
  setMetronomeEnabled(enabled) {
      if (this.isMetronomeEnabled === enabled) return; // Нет изменений

      this.isMetronomeEnabled = enabled;
      console.log(`[AudioEngine] Metronome ${enabled ? 'enabled' : 'disabled'}.`);

      if (enabled) {
          // Создаем синтезаторы, если их нет
          if (!this.metronomeSynth) {
              this.metronomeSynth = new Tone.MembraneSynth({
                  pitchDecay: 0.01,
                  octaves: 1,
                  oscillator: { type: 'sine' },
                  envelope: { attack: 0.001, decay: 0.1, sustain: 0, release: 0.05 }
              }).toDestination();
              this.metronomeSynth.volume.value = -20; // Тихий клик
          }
          if (!this.accentMetronomeSynth) {
              this.accentMetronomeSynth = new Tone.MembraneSynth({
                  pitchDecay: 0.01,
                  octaves: 2, // Немного выше/ярче для акцента
                  oscillator: { type: 'sine' },
                  envelope: { attack: 0.001, decay: 0.15, sustain: 0, release: 0.05 }
              }).toDestination();
              this.accentMetronomeSynth.volume.value = -14; // Акцент погромче
          }
      } else {
          // Уничтожаем синтезаторы
          if (this.metronomeSynth) {
              this.metronomeSynth.dispose();
              this.metronomeSynth = null;
          }
          if (this.accentMetronomeSynth) {
              this.accentMetronomeSynth.dispose();
              this.accentMetronomeSynth = null;
          }
      }

      // Переподготавливаем секвенсор, если он инициализирован и есть данные
      // Важно: это нужно делать, чтобы добавить/удалить запланированные события метронома
      if (this.initialized && this.currentSequencerStructure && this.currentTrackNames && this.currentCellsState && this.currentBarBlockMapping) {
           console.log('[AudioEngine] Re-preparing sequencer due to metronome state change.');
           // Сохраняем текущее состояние и время, если играло
           const wasPlaying = this.isPlaying;
           let capturedTime = 0;
           if (wasPlaying) {
               capturedTime = this.getCurrentTransportTime();
               // Не останавливаем транспорт, просто перепланируем события
           }

           // Переподготовка планировщика с текущими данными
           this.prepareSequencer(
               this.currentSequencerStructure,
               this.currentTrackNames,
               this.currentCellsState,
               this.currentBarBlockMapping // Передаем сохраненное mapping
           );

           // Перезапуск не требуется, т.к. транспорт не останавливался
           // if (wasPlaying) {
           //     this.play(capturedTime); // Перезапуск с нужного места, если нужно было бы останавливать
           // }
      } else {
           console.log('[AudioEngine] Skipping re-prepare for metronome (missing data or not initialized).');
      }
  }

  // Подготовка данных секвенсора к воспроизведению
  // Обновляем сигнатуру для приема barBlockMapping
  prepareSequencer(sequencerStructure, trackNames, cellsState, barBlockMapping) {
    // Сохраняем текущие данные для возможной переподготовки (например, при вкл/выкл метронома)
    this.currentSequencerStructure = sequencerStructure;
    this.currentTrackNames = trackNames;
    this.currentCellsState = cellsState;
    this.currentBarBlockMapping = barBlockMapping; // Сохраняем mapping

    this.transport.cancel(); // Отменяем все предыдущие запланированные события

    if (this.bpm <= 0) {
      console.warn("AudioEngine: BPM is not positive. Timing calculations might be incorrect.");
      return false;
    }

    const quarterNoteSecs = Tone.Time('4n').toSeconds(); // Длительность четвертной ноты (одного блока) в секундах

    if (quarterNoteSecs <= 0 || !isFinite(quarterNoteSecs)) {
        console.warn("AudioEngine: Calculated quarterNoteSecs is invalid. BPM:", this.bpm);
        return false; // Невозможно корректно рассчитать время
    }

    // Очищаем и строим эффективную структуру и карту индексов
    this.effectiveSequencerStructure = [];
    this.originalIndexMapping = [];
    const N = sequencerStructure.length;

    // Строим эффективную структуру на основе loopedOriginalIndices
    if (this.loopedOriginalIndices.length > 0 && N > 0) {
      console.log(`AudioEngine: Building effective structure from looped indices: [${this.loopedOriginalIndices.join(', ')}]`);
      this.loopedOriginalIndices.forEach(originalIndex => {
        if (originalIndex >= 0 && originalIndex < N) {
          this.effectiveSequencerStructure.push(sequencerStructure[originalIndex]);
          this.originalIndexMapping.push(originalIndex);
        } else {
          console.warn(`AudioEngine: Invalid index ${originalIndex} found in loopedOriginalIndices. Skipping.`);
        }
      });
      if (this.effectiveSequencerStructure.length > 0) {
         console.log(`AudioEngine: Effective loop structure created. Original indices in order: [${this.originalIndexMapping.join(', ')}]. Total effective blocks: ${this.effectiveSequencerStructure.length}`);
      } else {
           console.warn("AudioEngine: Effective loop structure is empty after processing looped indices.");
      }
      
    } else { // Нет лупа или N=0, используем всю секвенцию
      this.effectiveSequencerStructure = [...sequencerStructure];
      this.originalIndexMapping = sequencerStructure.map((_, idx) => idx);
      if (N>0) console.log("AudioEngine: Full sequence mode. Total effective blocks: ", this.effectiveSequencerStructure.length);
    }
    
    if (this.effectiveSequencerStructure.length === 0) {
        console.warn("AudioEngine: Effective sequencer structure is empty. No events will be scheduled.");
        this.transport.loopEnd = 0; // Убедимся, что transport.loopEnd корректен
        return false;
    }

    // --- Начало: Логика планирования метронома ---
    // Создаем сет оригинальных индексов блоков, которые начинают такт
    const barStartBlockIndices = new Set();
    if (barBlockMapping && typeof barBlockMapping === 'object') {
        Object.values(barBlockMapping).forEach(blockIndices => {
            if (Array.isArray(blockIndices) && blockIndices.length > 0) {
                barStartBlockIndices.add(blockIndices[0]);
            }
        });
    } else {
        console.warn("[AudioEngine] barBlockMapping is missing or invalid. Metronome accentuation might be incorrect.");
    }


    // Планируем клики метронома, если он включен
    if (this.isMetronomeEnabled && this.metronomeSynth && this.accentMetronomeSynth) {
        console.log("[AudioEngine] Scheduling metronome clicks.");
        this.effectiveSequencerStructure.forEach((_, tempBlockIndex) => {
            const originalBlockIndex = this.originalIndexMapping[tempBlockIndex];
            const beatStartTime = tempBlockIndex * quarterNoteSecs;
            const isStrongBeat = barStartBlockIndices.has(originalBlockIndex);

            this.transport.schedule(time => {
                const synth = isStrongBeat ? this.accentMetronomeSynth : this.metronomeSynth;
                // Воспроизводим короткий звук клика
                synth.triggerAttackRelease('C5', '64n', time);
            }, beatStartTime);
        });
    } else if (this.isMetronomeEnabled) {
         console.warn("[AudioEngine] Metronome is enabled, but synths are not ready. Skipping scheduling.");
    }
    // --- Конец: Логика планирования метронома ---

    // Планируем события на основе effectiveSequencerStructure
    this.effectiveSequencerStructure.forEach((blockDefinition, tempBlockIndex) => {
      if (!blockDefinition || !blockDefinition.trackSteps) return;

      const originalBlockIndex = this.originalIndexMapping[tempBlockIndex]; // Получаем оригинальный индекс блока

      // --- Планирование аудио событий ---
      trackNames.forEach(trackName => {
        const cellCount = blockDefinition.trackSteps[trackName];
        if (!cellCount || typeof cellCount !== 'number' || cellCount <= 0) return;

        // Пропускаем планирование аудио, если трек замьютирован
        if (this.mutedTracks.has(trackName)) return;

        const singleCellDurationSecs = (1 / cellCount) * quarterNoteSecs;

        for (let step = 0; step < cellCount; step++) {
          // Используем originalBlockIndex для формирования cellId
          const cellId = `${originalBlockIndex}-${trackName}-${step}`;
          const cell = cellsState[cellId];

          if (cell && cell.instrumentId) {
            // Время начала ячейки относительно начала ЭФФЕКТИВНОЙ последовательности
            const cellStartTimeInEffectiveSequenceSecs = (tempBlockIndex + (step / cellCount)) * quarterNoteSecs;

            this.transport.schedule(time => { 
              const instrument = this.getInstrument(cell.instrumentId);
              if (instrument) {
                instrument.triggerAttackRelease("C4", singleCellDurationSecs, time);
              }
            }, cellStartTimeInEffectiveSequenceSecs);
          }
        }
      });

      // --- Планирование маркеров для визуализации воспроизведения ---
      let maxStepsInBlock = 0;
      Object.values(blockDefinition.trackSteps).forEach(sc => {
        if (typeof sc === 'number' && sc > maxStepsInBlock) maxStepsInBlock = sc;
      });
      if (maxStepsInBlock === 0 && Object.keys(blockDefinition.trackSteps).length > 0) {
          maxStepsInBlock = 1; 
      }

      for (let visualStep = 0; visualStep < maxStepsInBlock; visualStep++) {
        const visualMarkerTimeInEffectiveSequenceSecs = (tempBlockIndex + (visualStep / maxStepsInBlock)) * quarterNoteSecs;
        
        this.transport.schedule(time => {
          // Получаем имя трека для этого шага (предполагаем, что шаг относится ко всем трекам для визуализации)
          // Проверяем, замьютированы ли ВСЕ треки, имеющие этот шаг (это упрощение, можно сделать точнее)
          let isAnyTrackActiveAtStep = false;
          trackNames.forEach(trackName => {
             if (!this.mutedTracks.has(trackName) && blockDefinition.trackSteps[trackName] >= maxStepsInBlock) {
               isAnyTrackActiveAtStep = true;
             }
          });

          // Обновляем позицию, только если хотя бы один трек на этом шаге не замьютирован
          // И если есть колбэк
          if (this.onPositionChange && isAnyTrackActiveAtStep) {
            // Отправляем originalBlockIndex для корректного отображения в UI
            this.playbackPosition = { blockIndex: originalBlockIndex, stepIndex: visualStep };
            this.onPositionChange(this.playbackPosition);
          }
        }, visualMarkerTimeInEffectiveSequenceSecs);
      }
    });

    this.transport.loop = true;
    this.transport.loopStart = 0; // Луп всегда начинается с 0 эффективной последовательности
    // Конец лупа - это общая длительность эффективной последовательности
    const totalEffectiveDurationSecs = this.effectiveSequencerStructure.length * quarterNoteSecs;
    this.transport.loopEnd = totalEffectiveDurationSecs; 
    
    // Обновляем логирование
    if (this.loopedOriginalIndices.length > 0 && this.effectiveSequencerStructure.length > 0) {
        console.log(`AudioEngine: Loop set from effective 0s to ${totalEffectiveDurationSecs.toFixed(2)}s (orig. blocks in order: [${this.originalIndexMapping.join(', ')}])`);
    } else {
        console.log(`AudioEngine: Full sequence loop from 0s to ${totalEffectiveDurationSecs.toFixed(2)}s`);
    }
    
    return true;
  }

  // Воспроизведение
  async play(startTime = this.transport.loopStart) {
    if (!this.initialized) {
      await this.initialize();
    }
    
    // Убедимся, что аудио контекст запущен (требуется взаимодействие с пользователем)
    await Tone.start(); 

    // Используем переданное startTime или значение по умолчанию
    const effectiveStartTime = typeof startTime === 'number' && isFinite(startTime) 
                               ? startTime 
                               : this.transport.loopStart;

    // Обновляем логирование
    if (this.loopedOriginalIndices.length > 0 && this.effectiveSequencerStructure.length > 0) {
      console.log(`AudioEngine: Запуск воспроизведения эффективной последовательности (Originals: ${this.originalIndexMapping.join(', ')}). Start time: ${effectiveStartTime.toFixed(2)}s`);
    } else {
      console.log(`AudioEngine: Запуск воспроизведения полной секвенции. Start time: ${effectiveStartTime.toFixed(2)}s`);
    }

    this.transport.start(undefined, effectiveStartTime); // Используем effectiveStartTime
    this.isPlaying = true;
    return true;
  }

  // Полная остановка (сброс позиции)
  stop() {
    this.transport.stop();
    this.isPlaying = false;
    // Сбрасываем позицию при остановке
    if (this.onPositionChange) {
      this.playbackPosition = null;
      this.onPositionChange(null);
    }
    return true;
  }

  // Переключение воспроизведения
  async togglePlay() {
    if (this.isPlaying) {
      this.stop();
    } else {
      // Перед запуском воспроизведения, убедитесь, что секвенсор подготовлен
      // с актуальными данными. Это может потребовать вызова prepareSequencer
      // из вашего React-компонента, если данные изменились.
      await this.play();
    }
    return this.isPlaying;
  }

  // Получение текущей позиции воспроизведения
  getPlaybackPosition() {
    return this.playbackPosition;
  }

  // Метод для получения текущего времени транспорта Tone.js
  getCurrentTransportTime() {
    return this.transport.seconds;
  }

  // Запуск предварительного отсчёта (preroll) перед началом воспроизведения
  async startPreroll(beatsCount = 4, onCompleteCallback = null) {
    // Остановим воспроизведение, если оно запущено
    // if (this.isPlaying) { // Логика остановки должна быть обработана вызывающей стороной (PlayerMenu)
    //   this.stop(); 
    // }

    // Инициализируем Tone.js, если еще не инициализирован
    if (!this.initialized) {
      await this.initialize();
    }

    // Запускаем аудиоконтекст (требует взаимодействия с пользователем)
    await Tone.start();
    
    // Устанавливаем флаг активного преролла
    this.isPrerollActive = true;
    
    // Очищаем существующие таймауты преролла
    this.prerollTimeouts.forEach(id => clearTimeout(id));
    this.prerollTimeouts = [];
    
    // Удаляем предыдущие синтезаторы, если они были
    if (this.prerollSynths) {
      if (this.prerollSynths.clickSynth) this.prerollSynths.clickSynth.dispose();
      if (this.prerollSynths.accentSynth) this.prerollSynths.accentSynth.dispose();
    }
    
    // Создаем синтезатор для звука метронома
    const clickSynth = new Tone.MembraneSynth({
      pitchDecay: 0.008,
      octaves: 2,
      oscillator: { type: 'sine' },
      envelope: {
        attack: 0.001,
        decay: 0.3,
        sustain: 0,
        release: 0.1
      }
    }).toDestination();
    clickSynth.volume.value = -10;

    // Создаем акцентированный звук для первой доли
    const accentSynth = new Tone.MembraneSynth({
      pitchDecay: 0.008,
      octaves: 3,
      oscillator: { type: 'sine' },
      envelope: {
        attack: 0.001,
        decay: 0.3,
        sustain: 0,
        release: 0.1
      }
    }).toDestination();
    accentSynth.volume.value = -6;
    
    // Сохраняем ссылки на синтезаторы
    this.prerollSynths = { clickSynth, accentSynth };

    const beatDurationSec = 60 / this.bpm;

    for (let beat = 0; beat < beatsCount; beat++) {
      const time = Tone.now() + beat * beatDurationSec;
      if (beat === 0) {
        accentSynth.triggerAttackRelease('C3', '16n', time);
      } else {
        clickSynth.triggerAttackRelease('C4', '16n', time);
      }

      // Сообщаем о текущей доле
      if (this.onPrerollBeat) {
        // Tone.Draw.schedule гарантирует, что колбэк будет вызван в правильное время
        // относительно аудио событий Tone.js, что важно для синхронизации UI.
        Tone.Draw.schedule(() => {
          this.onPrerollBeat(beat, beatsCount);
        }, time);
      }
    }
    
    const prerollTotalDurationMs = beatsCount * beatDurationSec * 1000;

    const completeTimeout = setTimeout(() => {
      // Сбрасываем флаг активного преролла
      this.isPrerollActive = false;
      
      // Очищаем ресурсы
      if (this.prerollSynths) {
        if (this.prerollSynths.clickSynth) this.prerollSynths.clickSynth.dispose();
        if (this.prerollSynths.accentSynth) this.prerollSynths.accentSynth.dispose();
        this.prerollSynths = null;
      }
      
      // Если установлен колбэк для обновления позиции - обнуляем,
      // так как реальное воспроизведение еще не началось или было только что сброшено.
      if (this.onPositionChange) {
        this.onPositionChange(null);
      }

      if (onCompleteCallback) {
        onCompleteCallback();
      }
    }, prerollTotalDurationMs);
    
    // Сохраняем идентификатор timeout
    this.prerollTimeouts.push(completeTimeout);

    // Сообщаем о начале preroll и количестве ударов
    if (this.onPrerollStart) {
      this.onPrerollStart(beatsCount);
    }
    
    return true; // Сигнализирует, что процесс преролла инициирован
  }
  
  // Отмена активного преролла
  cancelPreroll() {
    if (!this.isPrerollActive) return false;
    
    console.log("AudioEngine: Отмена преролла");
    
    // Очищаем все таймауты
    this.prerollTimeouts.forEach(id => clearTimeout(id));
    this.prerollTimeouts = [];
    
    // Останавливаем синтезаторы
    if (this.prerollSynths) {
      if (this.prerollSynths.clickSynth) {
        this.prerollSynths.clickSynth.dispose();
      }
      if (this.prerollSynths.accentSynth) {
        this.prerollSynths.accentSynth.dispose();
      }
      this.prerollSynths = null;
    }
    
    // Сбрасываем флаг активного преролла
    this.isPrerollActive = false;
    
    // Уведомляем об отмене преролла через колбэк
    if (this.onPrerollCancel) {
      this.onPrerollCancel();
    }
    
    return true;
  }
}

// Создаем синглтон аудио движка
const audioEngine = new AudioEngine();
export default audioEngine; 