import React, { useRef } from 'react';
import { useLocation } from 'react-router-dom';
import SequencerModule from '../modules/sequencer/SequencerModule';

const SequencerPage = () => {
  const sequencerModuleRef = useRef(null);
  const location = useLocation();

  const initialSessionData = location.state?.loadedSessionData || null;

  const handleExportClick = () => {
    const sessionName = window.prompt("Введите имя для сохранения сессии:");
    if (sessionName && sessionName.trim() !== "") {
      let folderNameInput = window.prompt("Введите имя папки (оставьте пустым или нажмите Отмена для сохранения в корень):");
      // Если пользователь нажал "Отмена" (folderNameInput === null) или оставил поле пустым, 
      // то folderName будет пустой строкой, что на бэкенде превратится в null.
      const folderName = folderNameInput ? folderNameInput.trim() : ""; 

      if (sequencerModuleRef.current) {
        sequencerModuleRef.current.exportData(sessionName.trim(), folderName);
      }
    } else if (sessionName !== null) { // Нажал ОК, но имя пустое
      alert("Имя сессии не может быть пустым.");
    }
    // Если sessionName === null (нажал Отмена для имени сессии), ничего не делаем
  };

  const buttonStyle = {
    backgroundColor: '#4CAF50', /* Green */
    border: 'none',
    color: 'white',
    padding: '10px 20px',
    textAlign: 'center',
    textDecoration: 'none',
    display: 'inline-block',
    fontSize: '16px',
    margin: '4px 2px',
    marginBottom: '10px',
    cursor: 'pointer',
    borderRadius: '8px',
    boxShadow: '0 2px 4px 0 rgba(0,0,0,0.2)',
    transitionDuration: '0.4s',
  };

  return (
    <div className="sequencer-page">
      <h1>Секвенсор {initialSessionData ? `(${initialSessionData.sessionName})` : ''}</h1>
      <button onClick={handleExportClick} style={buttonStyle}>
        Сохранить на сервер
      </button>
      <SequencerModule ref={sequencerModuleRef} initialSessionData={initialSessionData} />
    </div>
  );
};

export default SequencerPage; 